// src/config/firebase.ts
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore";

//  .env (VITE_FIREBASE_API_KEY, )
//     .env 
const firebaseConfig = {
    apiKey: import.meta.env.VITE_FIREBASE_API_KEY || "_API_KEY__FIREBASE_CONSOLE",
    authDomain: import.meta.env.VITE_FIREBASE_AUTH_DOMAIN || "_AUTH_DOMAIN",
    projectId: import.meta.env.VITE_FIREBASE_PROJECT_ID || "_PROJECT_ID",
    storageBucket: import.meta.env.VITE_FIREBASE_STORAGE_BUCKET || "_STORAGE_BUCKET",
    messagingSenderId: import.meta.env.VITE_FIREBASE_MESSAGING_SENDER_ID || "_MESSAGING_SENDER_ID",
    appId: import.meta.env.VITE_FIREBASE_APP_ID || "_APP_ID"
};

//  Firebase App
const app = initializeApp(firebaseConfig);

// Export Database 
export const db = getFirestore(app);
