import { useState, useEffect, useRef } from 'react'; // 1.  useRef
import liff from '@line/liff';
import type { User } from '../types/user.types';

const STORAGE_KEY = 'travelApp_user';
const LIFF_ID = import.meta.env.VITE_LIFF_ID || "";

export function useAuth() {
    const [user, setUser] = useState<User | null>(null);
    const [isLoading, setIsLoading] = useState(true);

    // 2.  Ref 
    const isInitCalled = useRef(false);

    useEffect(() => {
        // 3.  init   ( Strict Mode )
        if (isInitCalled.current) return;
        isInitCalled.current = true;

        const initLiff = async () => {
            try {
                if (!LIFF_ID) {
                    console.warn("  VITE_LIFF_ID");
                    throw new Error("VITE_LIFF_ID is missing");
                }

                //  LIFF
                await liff.init({ liffId: LIFF_ID });

                //  Login  
                if (liff.isLoggedIn()) {
                    const profile = await liff.getProfile();
                    const lineUser: User = {
                        id: profile.userId,
                        name: profile.displayName,
                        avatar: profile.pictureUrl || 'https://api.dicebear.com/9.x/micah/svg?seed=Default'
                    };
                    setUser(lineUser);
                    localStorage.setItem(STORAGE_KEY, JSON.stringify(lineUser));
                } else {
                    //  Login  LINE App  Auto Login  (Optional)
                    // liff.login();
                }
            } catch (err) {
                console.error(' LIFF Init Failed:', err);
                //  code invalid  URL   user 
                if (window.location.search.includes("code=")) {
                    window.history.replaceState(null, "", window.location.pathname);
                    window.location.reload();
                }
            } finally {
                setIsLoading(false);
            }
        };

        initLiff();
    }, []);

    const loginWithLine = () => {
        if (!LIFF_ID) return;
        if (!liff.isLoggedIn()) {
            liff.login();
        }
    };

    const logout = () => {
        liff.logout();
        localStorage.removeItem(STORAGE_KEY);
        setUser(null);
        window.location.reload();
    };

    const updateProfile = (updatedUser: User) => {
        setUser(updatedUser);
        localStorage.setItem(STORAGE_KEY, JSON.stringify(updatedUser));
    };

    return {
        user,
        isLoading,
        loginWithLine,
        updateProfile,
        logout
    };
}
